@Component
export default class Registration extends Vue {
  data() {
    return {
      picker: new Date().toISOString().substr(0, 10),
      treatDate: new Date().toISOString().substr(0, 10),
      birthDate: "1900-01-01",
      treatMenu: false,
      birthMenu: false
    };
  }

  registrationInfo: Object = {
    doctorName: "黃俊凱",
    totleRegistration: 87,
    nowRegistration: 78
  };
}
